import React, { PureComponent } from 'react';
import { connect } from 'react-redux';

import { STORE_KEY } from '../../constants';
import { getFileDuration } from '../../selectors';
import { setCurrentTime } from '../../actions';

class Annotation extends PureComponent {
  render() {
    const { startX, width, cssClass = 'defaultAnnotation' } = this.props;

    return (
      <div
        className={'annotationMini ' + cssClass}
        style={{
          transform: `translateX(${startX}px)`,
          width,
        }}
      />
    );
  }
}

class AnnotationsMinimap extends PureComponent {
  jumpTo = e => {
    const desiredTime =
      e.nativeEvent.offsetX *
      this.props.fileDuration /
      this.props.scrollerWidth;
    this.props.dispatch(setCurrentTime(desiredTime));
  };

  render() {
    const { annotationsList, secondWidth, currentTime } = this.props;
    const minimapCurrentPosition = currentTime * secondWidth;

    return (
      <div className="annotationsMinimap" onClick={this.jumpTo}>
        {annotationsList.map(annotation => (
          <Annotation
            key={annotation.id}
            startX={annotation.time * secondWidth}
            width={annotation.duration * secondWidth}
            cssClass={annotation.cssClass}
          />
        ))}
        <div
          className="cursorMinimap"
          style={{
            transform: `translateX(${minimapCurrentPosition}px)`,
          }}
        />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  annotationsList: state.annotations.list,
  secondWidth:
    state.presentation.scrollerWidth /
    (state.header.dataRecordsNumber * state.header.dataRecordDuration || 1),
  currentTime: state.navigation.currentTime,
  scrollerWidth: state.presentation.scrollerWidth,
  fileDuration: getFileDuration(state),
});

export default connect(mapStateToProps, undefined, undefined, {
  storeKey: STORE_KEY,
})(AnnotationsMinimap);
